.dialogs {
  display: grid;
  grid-template-columns: 1fr 5fr;
  grid-template-rows: 8fr;
  grid-template-areas:
    "dialogs_items" "maseege"
    "enter";
}

.dialogsItems {
  padding: 10px;
  grid-area: dialogs_items;
  grid-area: 1/1/2/2;
  color: gold;
}

.dialog {
  padding: 10px;
  grid-area: maseege;
  justify-content: end;
}

.dialog a {
  text-decoration: none;
}

.dialog a:visited {
  text-decoration: none;
  color: rgb(73, 54, 148);
}

.enter {
  height: 200px;
  grid-area: enter;
  grid-column-start: 2;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  padding-right: 20px;
}

.maseeges {
  display: flex;
  align-items: flex-end;
  flex-direction: column;
}

.maseege {
  padding: 15px 50px 10px;
}

.button {
  padding-top: 15px;
  display: flex;
  justify-content: flex-end;
}

.buttonSend {
  background-color: #83bb75;
  border-radius: 15px;
  width: 70px;
  height: 25px;
}
